.todo-expired {
  /* color: var(--main-color); */
  color: green;
}

.todo__btn-expired {
  background-color: var(--main-color);
  opacity: 1;
}

.todo__btn-expired:hover {
  transition: all 0.5s;
  background-color: var(--main-color);
  opacity: 0.9;
}

.todo__btn-expired:active {
  transition: all 0.5s;
  background-color: var(--main-color);
  opacity: 0.8;
}

.todo-expired__content {
  color: rgb(54, 0, 0);
  text-decoration: line-through;
  text-decoration-thickness: 1px;
}
.todo__btn-expire {
  cursor: default;
}
.todo__btn-expired__highlight {
  background-color: red;
}
.todo__btn-delete__expired {
  -webkit-animation: backgroundColorLoop 2s infinite;
          animation: backgroundColorLoop 2s infinite;
}
@-webkit-keyframes backgroundColorLoop {
  0% {
    color: var(--main-bg-color);
  }
  50% {
    color: rgb(255 167 125);
  }
  100% {
    color: var(--main-bg-color);
  }
}
@keyframes backgroundColorLoop {
  0% {
    color: var(--main-bg-color);
  }
  50% {
    color: rgb(255 167 125);
  }
  100% {
    color: var(--main-bg-color);
  }
}
@media all and (orientation: portrait) {
  /* //해당 비교에 따라 우선순위 별표의 위치조정을 실행한다. */
  .todo__star-rating {
    margin-left: calc(2.25vh / 2.75);
  }
  .todo__btn-box {
    margin-left: calc(2.25vh / 1.25);
  }
  .todo__btn-box {
    justify-content: flex-start;
  }
  .todo__explain-btn {
    font-size: 6px;
  }
  .todo__content {
    font-size: 10px;
  }
  .todo__text {
    width: 30%;
  }
  .todo__btn {
    /* padding: 4px; */
    /* margin-left: 2%; */
    height: 24px;
    width: 27px;
    font-weight: bold;
    color: var(--main-bg-color);
    background-color: var(--main-sub-color);
  }
  .todo__btn-content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: black; */
  }
}
@media all and (orientation: landscape) {
  .todo__star-rating {
    margin-left: calc(1.25vw / 2.75);
  }
  .todo__btn-box {
    /* background-color: bl ue; */
    margin-left: calc(1.25vw / 1.25);
  }
}

.header {
